.ffblock-fluent-form-wrapper {
	--checkbox-radio-height: 22px;
	--checkbox-radio-width: 22px;

	&.ffblock-fm-hide-label {
		.ff-el-group {

			label:not(.ff-el-form-check-label),
			.ff-el-input--label {
				display: none;
			}
		}
	}

	&.ffblock-fm-hide-error-message {
		.error.text-danger {
			display: none;
		}
	}

	&.ffblock-fm-hide-placeholder {

		input::-webkit-input-placeholder,
		textarea::-webkit-input-placeholder {
			opacity: 0;
			visibility: hidden;
		}

		input::-moz-placeholder,
		textarea::-moz-placeholder {
			opacity: 0;
			visibility: hidden;
		}

		input:-ms-input-placeholder,
		textarea:-ms-input-placeholder {
			opacity: 0;
			visibility: hidden;
		}

		input:-moz-placeholder,
		textarea:-moz-placeholder {
			opacity: 0;
			visibility: hidden;
		}
	}

	.ff-el-input--content input[type=checkbox],
	.ff-el-input--content input[type=radio] {
		align-items: center;
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;
		background: #fff;
		border: 1px solid #abb8c3;
		border-radius: .2em;
		-webkit-border-radius: .2em;
		color: #333;
		cursor: pointer;
		display: inline-flex;
		height: var(--checkbox-radio-height, 22px);
		justify-content: center;
		line-height: 0;
		margin: 0;
		position: relative;
		text-align: center;
		transition: all .2s linear;
		-webkit-transition: all .2s linear;
		width: var(--checkbox-radio-width, 22px);
	}

	.ff-el-input--content input[type=radio] {
		background-color: #fafafa;
		border-radius: 50%;
	}

	.ff-el-input--content input[type=checkbox]:checked:before,
	.ff-el-input--content input[type=radio]:checked:before {
		align-items: center;
		content: "âœ”";
		display: inline-flex;
		font-size: calc(var(--checkbox-radio-width, 22px)/1.2);
		height: 100%;
		justify-content: center;
		margin: 0;
		padding: 0;
		position: absolute;
		width: 100%;
	}

	.ff-el-input--content input[type=radio]:checked:before {
		background-color: #333;
		border-radius: 50%;
		content: "";
		height: calc(var(--checkbox-radio-width, 22px)/2);
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		width: calc(var(--checkbox-radio-width, 22px)/2);
	}

	.ff-el-form-check {
		label.ff-el-form-check-label {
			display: inline-flex;
			gap: 8px;
			align-items: center;
			margin: 0 !important;
			vertical-align: middle;

			.ff-el-form-check-input {
				top: 0;
			}
		}

		&.ff-el-tc {
			label.ff-el-form-check-label {
				align-items: baseline;
			}
		}
	}


}

body {
	.ffblock-block-editor {
		@media (min-width: 1025px) {
			&.ffblock-hide-desktop {
				//display: inherit;
				background-image: repeating-linear-gradient(125deg,
						rgba(0, 0, 0, 0.35),
						rgba(0, 0, 0, 0.08) 1px,
						transparent 2px,
						transparent 8px);
				border: 1px solid rgba(0, 0, 0, 0.025);
			}
		}

		@media (min-width: 768px) and (max-width: 1024px) {
			&.ffblock-hide-tablet {
				display: inherit;
				background-image: repeating-linear-gradient(125deg,
						rgba(0, 0, 0, 0.35),
						rgba(0, 0, 0, 0.08) 1px,
						transparent 2px,
						transparent 8px);
				border: 1px solid rgba(0, 0, 0, 0.025);
			}
		}

		@media (max-width: 767px) {
			&.ffblock-hide-mobile {
				display: inherit;
				background-image: repeating-linear-gradient(125deg,
						rgba(0, 0, 0, 0.35),
						rgba(0, 0, 0, 0.08) 1px,
						transparent 2px,
						transparent 8px);
				border: 1px solid rgba(0, 0, 0, 0.025);
			}
		}
	}

	.ffblock-block-frontend {
		@media (min-width: 1025px) {
			&.ffblock-hide-desktop {
				display: none !important;
			}
		}

		@media (min-width: 768px) and (max-width: 1024px) {
			&.ffblock-hide-tablet {
				display: none !important;
			}
		}

		@media (max-width: 767px) {
			&.ffblock-hide-mobile {
				display: none !important;
			}
		}
	}
}